  .footer__social {
    padding: 0;
    margin: -16px 0 0;
    max-width: 182px;
  }
}
@media (max-width: 999px) {
  .footer__social {
    margin: 0;
  }
  .footer__social span {
    margin: 7px 0 14px 6px;
  }
  .footer__social ul {
    max-width: 128px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .footer__social {
    margin: 0 0 24px;
    max-width: 100%;
  }
  .footer__social span {
    margin: -2px 0 19px 7px;
  }
  .footer__social ul {
    max-width: 161px;
    margin: 0 0 0 10px;
  }
}

.footer__social-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.footer__social-link:hover, .footer__social-link:focus {
  opacity: 0.9;
}

.footer__logo {
  grid-area: L;
  width: 244px;
  height: 132px;
  padding: 20px 0 0 24px;
  cursor: pointer;
}
.footer__logo:hover, .footer__logo:focus {
  opacity: 0.8;
}
@media (max-width: 1439px) {
  .footer__logo {
    padding: 0;
    margin: -50px 0 0 -13px;
    width: 217px;
  }
}
@media (max-width: 999px) {
  .footer__logo {
    margin: 16px 0 0 -15px;
    width: 222px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin: 33px 0 0 6px;
    margin: 34px 5px 0 0;
